<template>
  <view class="history-page">
    <!-- È°∂ÈÉ®Ê†áÈ¢òÊ†è -->
    <view class="header">
      <text class="header-title">ÊàëÁïôË®ÄËøáÁöÑÂïÜÂìÅ</text>
    </view>
    <!-- ÂïÜÂìÅÂàóË°® -->
    <scroll-view scroll-y class="goods-list">
      <view class="goods-grid">
        <view 
          class="goods-item" 
          v-for="(item, index) in historyList" 
          :key="index"
          @click="gotoDetail(item.goods_id)"
        >
          <image :src=item.goods_big_logo class="goods-image" mode="aspectFill"></image>
          <view class="goods-price">
            <text class="price-symbol">¬•</text>
            <text class="price-number">{{ item.goods_price }}</text>
          </view>
        </view>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view v-if="historyList.length === 0" class="empty-state">
        <text class="empty-text">ÊöÇÊó†{{ tabs[activeTab] }}ËÆ∞ÂΩï</text>
      </view>
    </scroll-view>
  </view>
</template>

<script>
	import { mapState } from 'vuex'
import { computed } from "vue";
export default {
  data() {
    return {
      activeTab: 0,
      tabs: ['ÊàëÁïôË®ÄÁöÑ'],
      commentedList: [],
    };
  },
  computed: {
	...mapState('m_user', ['token', 'code', 'userBase', 'openid']),
    historyList() {
      switch(this.activeTab) {
        case 0: return this.commentedList;
		default: return [];
      }
    },
  },
  async onLoad() {
    // ÂÆûÈôÖ‰ΩøÁî®Êó∂Âú®ËøôÈáåË∞ÉÁî®Êé•Âè£Ëé∑ÂèñÊï∞ÊçÆ
    await this.loadHistory();
	await this.getHistory();
  },
  methods: {    
    // Ë∑≥ËΩ¨Âà∞ÂïÜÂìÅËØ¶ÊÉÖ
    async gotoDetail(goods_id) {
		try {
		  // 1. ÂÖàÂ¢ûÂä†ÊµèËßàÊ¨°Êï∞
		  console.log('üìä Â¢ûÂä†ÂïÜÂìÅÊµèËßàÊ¨°Êï∞:', goods_id);
		  const { data: res } = await uni.$http.post('/goods/view', {
		    goods_id: goods_id,
		    // ÂèØÈÄâÔºöÊ∑ªÂä†Áî®Êà∑‰ø°ÊÅØÁî®‰∫éÁªüËÆ°ÂàÜÊûê
		    user_openid: this.openid || null
		  });
		
		  if (res.meta.status === 200) {
		    console.log('‚úÖ ÊµèËßàÊ¨°Êï∞Â¢ûÂä†ÊàêÂäü');
		  } else {
		    console.warn('‚ö†Ô∏è ÊµèËßàÊ¨°Êï∞Â¢ûÂä†Â§±Ë¥•:', res.meta.msg);
		  }
		} catch (error) {
		  console.error('‚ùå ÊµèËßàÊ¨°Êï∞Â¢ûÂä†Êé•Âè£ÈîôËØØ:', error);
		  // Âç≥‰ΩøÊé•Âè£Â§±Ë¥•‰πüÁªßÁª≠Ë∑≥ËΩ¨Ôºå‰∏çÂΩ±ÂìçÁî®Êà∑‰ΩìÈ™å
		}
      uni.navigateTo({
        url: `/subpkg/goods_detail/goods_detail?goods_id=${goods_id}`
      });
    },
    
    // ÂÆûÈôÖ‰ΩøÁî®Êó∂ÁöÑÊé•Âè£Ë∞ÉÁî®Á§∫‰æã
    async loadHistory() {
      try {
		  let res;
		  const queryObj = {code: this.openid};
		  switch(this.activeTab)  {
			  case 0: const { data: commentData } = await uni.$http.get('/users/comment', queryObj);
			  res = commentData;
			  break;
			  
		  }
        if (res.meta.status === 200) {
          switch(this.activeTab) {
            case 0: this.commentedList = res.data; break;
          }
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂéÜÂè≤ËÆ∞ÂΩïÂ§±Ë¥•:', error);
      }
    },
	
	async getHistory() {
		try {
			let res;
			const queryObj = {goods_id: this.historyList}
			switch(this.activeTab) {
				case 0: const { data: commentData1 } = await uni.$http.get('/goods/historysearch', queryObj);
				res = commentData1;
				break;
			}
			if (res.meta.status === 200) { 
				switch(this.activeTab) {
				  case 0: this.commentedList = res.message; break;
				}
			}
		} catch (error) {
			console.log('Âä†ËΩΩÂéÜÂè≤ËÆ∞ÂΩïÂ§±Ë¥•:', error);
		}
	},
    
    async clearHistoryAPI() {
      try {
        const { data: res } = await uni.$http.post('/user/history/clear', {
          type: this.activeTab
        });
        if (res.meta.status === 200) {
          uni.showToast({ title: 'Ê∏ÖÁ©∫ÊàêÂäü', icon: 'success' });
        }
      } catch (error) {
        console.error('Ê∏ÖÁ©∫Â§±Ë¥•:', error);
      }
    }
  },
};
</script>

